import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { 
  MatAutocompleteModule,
  MatButtonModule,
  MatButtonToggleModule,
  MatCardModule,
  MatCheckboxModule,
  MatChipsModule,
  MatDatepickerModule,
  MatDialogModule,
  MatExpansionModule,
  MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule,
  MatNativeDateModule,
  MatPaginatorModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatRadioModule,
  MatRippleModule,
  MatSelectModule,
  MatSidenavModule,
  MatSliderModule,
  MatSlideToggleModule,
  MatSnackBarModule,
  MatSortModule,
  MatTableModule,
  MatTabsModule,
  MatToolbarModule,
  MatTooltipModule,} from '@angular/material';
import { DataSource, CdkTableModule } from '@angular/cdk/table';
import { FormsModule, ReactiveFormsModule }   from '@angular/forms';

/** Importing local modules */

/** Importing local components */
import { ScheduleComponent }    from './schedule.component';
import { ManageComponent, } from './manage.component';
import { ConfirmAssignmentEraseComponent } from  './manage/confirm-assignment-erase';
  
export const routes: Routes = [
  {
    path: '',
    component: ScheduleComponent,
      //canActivate: [AuthGuard],
    children: [
      { path: '', pathMatch: 'full', redirectTo: 'manage' },
      { path: 'manage', component: ManageComponent },
    //   { path: 'edit-user/:id', component: EditUserComponent },
    //   { path: 'create-user', component: EditUserComponent },
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
    CdkTableModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes),
  ],
  declarations: [
    ScheduleComponent,
    ManageComponent,
    ConfirmAssignmentEraseComponent 
  ],
    entryComponents: [
//        SelectVehicleComponent,
        ConfirmAssignmentEraseComponent,
    ],
  providers: []
})

export class ScheduleModule {}



