import { NgModule, HostListener} from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { Component, ViewChild, Inject, ElementRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatInputModule, MatPaginator} from '@angular/material';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import { DataSource } from '@angular/cdk/table';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
//import { VehicleService } from '../../../service/vehicle.service';
import { Observable } from 'rxjs/Rx';
import { LocalStorageService } from 'angular-2-local-storage';


/** importing Models */
//import { Vehicle } from '../../../model/vehicle';

@Component({
  templateUrl: './confirm-assignment-erase.html',
  styles: [`
    span {
        margin: 1em;
        margin-bottom: 8em;
    }

    div {
        height: 4em;
    }

    button {
        float:right;
        margin-right: 1em;
    }

    button:last-of-type {
        margin-right: 0em;
    }
  `]
})

export class ConfirmAssignmentEraseComponent{

  private assignmentId = -1;
  private yesCallback: Function;
  @ViewChild('defaultButton') private defaultButton: ElementRef;


  constructor (
    private dialogRef: MatDialogRef<ConfirmAssignmentEraseComponent>,
    private router: Router,
      //private userService: VehicleService,
    private localStorageService: LocalStorageService,
    @Inject(MAT_DIALOG_DATA) public data: any) {
        this.assignmentId = data.assignmentId;
        this.yesCallback = data.yesCallback;
  }
  
  ngOnInit() {
  }

  ngAfterViewInit() {
      setTimeout(() => {
          try{
              this.defaultButton.nativeElement.focus();
          } catch (e) {
              //...shhhh
          }
      }, 500);
  }

  private onNoClick(): void {
    this.dialogRef.close();
  };

  private onYesClick(): void {
    this.yesCallback();
    this.dialogRef.close();
  }
  
}



