    public computeDay(offsetLeft: number) {
        let result = Math.floor(offsetLeft / (this._hour_width * this._scale_factor * 24))+1;
        return result;
    }

    public computeWidth(shift: string) {
        let width = this._hour_width * this._scale_factor;
        switch (shift) {
            case 'AM':
            case 'PM':
                width *= 12;
                break;
            case 'D':
                width *= 24;
                break;
        }

        return width;
    }


    /** Computes assignment's offset from its left side in pixels. 
     * @param weekday Week day in number. Monday is 0 (contrary to Javascript's 1!)
     * @param shift Shift abbreviation. Examples: "D"
     *
     * @return offset in pixels
     */
    public computeOffsetLeft(weekday: number, shift: string) {
        weekday--;
        weekday = (weekday < 0) ? 6 : weekday;
        let coordinates;
        let dayLength = this._hour_width*this._scale_factor*24; //day length in pixels
        coordinates = dayLength * weekday;
        if(shift == 'PM') {
            coordinates += this._hour_width*this._scale_factor*12;
        }

        return coordinates;
        //remaining = Math.floor(coordinates) % (this._hour_width*this._scale_factor*12);
    }

    public array_flip(array: any[])
    {
        var key, tmp_ar = {};
        for ( key in array )
        {
            if ( array.hasOwnProperty( key ) )
            {
                tmp_ar[array[key]] = key;
            }
        }
        return tmp_ar;
    }

