            (this._hour_width * this._scale_factor);
        let daysPerScreen = hoursPerScreen / 24;
        this.hours.length = Math.ceil(hoursPerScreen) * 2;
        this.days.length = 0;
        let day;
        for(let i=0; i<this.hours.length; i++){
            this.hours[i] = i % 24;
            if(this.hours[i] == 0){
                day = date.fork();
                this.days.push(day);
                date.alterDate(+1);
            }
        }

        let vehicleIds: any[] = [];
        for(let sch of this.activitySchedule) {
                vehicleIds.push(sch.activity.id);
        }

        let timeLength = new TmLength();
        timeLength.seconds = this.hours.length * 3600;

        this._scaleView();
