    public deleteAssignmentLocally(assignmentId: number|string) {
        let vi = null;
        let ai = null;

        let vc = 0;
        let ac = 0;
        SEEK_ASSIGNMENT:
        for(let vehicle of this.activitySchedule) {
            for (let ass of vehicle.assignments) {
                if(ass.assignmentId == assignmentId) {
                    vi = vc;
                    ai = ac;
                    break SEEK_ASSIGNMENT;
                }
                ac++;
            }
            vc++;
            ac=0;
        }

        if(vi!=null && ai!=null){
            this.activitySchedule[vi].assignments.splice(ai, 1);
            this.assignment_selected = null;
        }
    }

    public deleteAssignment(assignmentId: number|string) {
        this.schedule1Service.deleteAssignment(assignmentId, () => {
            this.deleteAssignmentLocally(assignmentId);
        });
    }

