    private bubbleSort(array: any[], property: string) {
        for(let i=(array.length -1); i >= 0; i--) {
            for(let j=1; j <=i; j++) {
                if (array[j-1][property]> array[j][property]) {
                    let tmp = array[j-1];
                    array[j-1] = array[j];
                    array[j] = tmp;
                }
            }
        }
    }

