                let elementCoords = this.elementCoords(this._resize_target);
                let relX = this.alignAssignment((<any>event).clientX-elementCoords.x, 'hour');
                let width = this.week_alignAssignment(relX, "meridiem_ceil");
                let maxWidth = this.computeWidth("D");
                width  = maxWidth > width ? width : maxWidth;
