    public elementCoords ( node: any ) {
        //let node = <any>event.target;
        if (node.getBoundingClientRect) {
            var rect = node.getBoundingClientRect();
            var sx = -(window.scrollX ? window.scrollX : window.pageXOffset);
            var sy = -(window.scrollY ? window.scrollY : window.pageYOffset);

            return {
                x: rect.left - sx,
                y: rect.top - sy,
                width: rect.width,
                height: rect.height,
            }
        }
    }

    public elementCoordsDocument ( node: any ) {
        //let node = <any>event.target;
        if (node.getBoundingClientRect) {
            var rect = node.getBoundingClientRect();
            var sx = -(window.scrollX ? window.scrollX : window.pageXOffset);
            var sy = -(window.scrollY ? window.scrollY : window.pageYOffset);

            sx -= this.scrolledParent.nativeElement.scrollLeft;
            sy -= this.scrolledParent.nativeElement.scrollTop;

            return {
                x: rect.left - sx,
                y: rect.top - sy
            }
        }
    }

    public computeStartTime(offsetLeft: number) {
        let offsetHours = offsetLeft / (this._scale_factor * this._hour_width);
        let startTime = this.days[0].fork();
        startTime.date.setMilliseconds(startTime.date.getMilliseconds() + (offsetHours*3600000));

        return startTime;
    }

    public computeTimeLength(width: number) {
        let hours = width / (this._scale_factor * this._hour_width);
        let tmLength = new TmLength();
        tmLength.setHours(hours);

        return tmLength;
    }

