    public week_alignAssignment(coordinates: number, granularity: string) {
        let remaining;
        switch(granularity) {
            case 'meridiem':
                remaining = Math.floor(coordinates) % (this._hour_width*this._scale_factor*12);
                coordinates -= remaining;
                break;
            case 'meridiem_ceil':
                remaining = Math.ceil(coordinates) % (this._hour_width*this._scale_factor*12);
                coordinates -= remaining;
                break;
            case 'day':
                remaining = Math.ceil(coordinates) % (this._hour_width*this._scale_factor*24);
                coordinates -= remaining;
                break;
        }

        return coordinates;
    }

    public alignAssignment(coordinates: number, granularity: string) {
        switch(granularity) {
            case 'hour':
                let remaining = Math.ceil(coordinates) % this._hour_width;
                coordinates -= remaining;
                break;
        }

        return coordinates;
    }

    public toggleWeekView() {
        this.b_week_view = !this.b_week_view;
        setTimeout(() => {
            this._scaleView();
            this.installCallbacks();
        }, 0);
    }
