export class Activity {
    id?: number;
    name: string;
    type?: any;
    description?: string; 
    scheduleStartDate?: any;
    scheduleEndDate?: any;
    activityShifts: any[];
    activitySpecialities?: any[];
}

export class ActivityType {
    static Training: string = 'Trainig';
    static Service: string = 'Service';
    static Late: string = 'Late';
    static Night: string = 'Night';
}



